import { useTranslation } from "react-i18next";
import style from "./Button.module.css";
const AdmissionButton = ({ label, count, onAction, customStyle }) => {
  const { t } = useTranslation();
  const onClick = (e) => {
    onAction(e);
  };
  return (
    <div className={style.btn_container} onClick={onClick} style={customStyle}>
      {count > 0 && (
        <span
          style={{
            marginRight: "0.8rem",
          }}
        >
          {count}
        </span>
      )}

      {t(label)}
    </div>
  );
};

export default AdmissionButton;
